#cover:after,
#theend:after {
  opacity: 0;
}
#cover h2,
#theend h2 {
  font-size: 70px;
  margin-bottom: 10px;
  line-height: 1.4;
  background-image: none;
  background-color: #fff;
  color: #6f12f4;
  -webkit-text-fill-color: unset;
}
#cover p,
#theend p {
  font-size: 24px;
  line-height: 1.4;
  padding: 10px 25px;
}
#cover a,
#theend a {
  color: #fff;
  opacity: 0.75;
}
#cover {
  background: #4e1982;
  background: linear-gradient(90deg, #4e1982 0%, #6507ed 100%);
}
.cover img {
  width: auto;
  height: auto;
  min-width: 100%;
  min-height: 100%;
}
.logo {
  width: 75px;
  background-color: #fff;
  padding: 5px;
}
.logo + .logo {
  margin-left: 30px;
}
